import React, { useState, useEffect } from 'react';
import { MdLocationOn, MdWork, MdEmail, MdSchool } from 'react-icons/md';
import {
  FaWhatsapp,
  FaNodeJs,
  FaReact,
  FaHtml5,
  FaCss3Alt,
  FaJsSquare,
} from 'react-icons/fa';

import api from '~/services/api';
import {
  Content,
  AboutContainer,
  List,
  Item,
  ItemImage,
  ItemSkill,
} from './styles';

export default function HomePage() {
  const [avatar, setAvatar] = useState('');
  const [resize, setResize] = useState(false);

  useEffect(() => {
    async function loadAvatar() {
      const { data } = await api.get('/users/igorjung');

      setAvatar(data.avatar_url);
    }

    const windowWidth = window.innerWidth;

    if (windowWidth <= 350) {
      setResize(true);
    }

    loadAvatar();
  }, []);

  return (
    <Content>
      <h1>About Me</h1>

      <AboutContainer>
        <div>
          <List>
            <ItemImage>
              <img src={avatar} alt="IgorJung" />
            </ItemImage>
            <Item>
              <MdLocationOn size={20} color={resize ? '#333' : '#ddd'} />
              <strong>Volta Redonda, Brazil</strong>
            </Item>
            <Item>
              <MdWork size={20} color={resize ? '#333' : '#ddd'} />
              <strong>Web developer</strong>
            </Item>
            <Item>
              <MdEmail size={20} color={resize ? '#333' : '#ddd'} />
              <strong>igor.jung@outlook.com</strong>
            </Item>
            <Item>
              <FaWhatsapp size={20} color={resize ? '#333' : '#ddd'} />
              <strong>+55 (24) 98848-2727</strong>
            </Item>
          </List>
        </div>
        <div>
          <h1>Education/ Certifications</h1>
          <List>
            <Item>
              <MdSchool size={20} color={resize ? '#333' : '#ddd'} />
              <div>
                <strong>IFRJ - Instituto Federal do Rio de Janeiro</strong>
                <p>2016-2019</p>
              </div>
            </Item>
            <Item>
              <MdSchool size={20} color={resize ? '#333' : '#ddd'} />
              <div>
                <strong>Rocketseat</strong>
                <p>2019</p>
              </div>
            </Item>
          </List>
          <h1>Skills</h1>
          <List>
            <Item>
              <ItemSkill>
                <div>
                  <FaHtml5 size={20} color="#F16528" />
                  <p>HTML5</p>
                </div>
                <div>
                  <FaCss3Alt size={20} color="#56A3E7" />
                  <p>CSS3</p>
                </div>
                <div>
                  <FaJsSquare size={20} color="	#F7DF1D" />
                  <p>JavaScript</p>
                </div>
              </ItemSkill>
            </Item>
            <Item>
              <ItemSkill>
                <div>
                  <FaNodeJs size={20} color="#90C63D" />
                  <p>NodeJs</p>
                </div>
                <div>
                  <FaReact size={20} color="#5CDEF9" />
                  <p>ReactJs</p>
                </div>
                <div>
                  <FaReact size={20} color="#34AAC8" />
                  <p>ReactNative</p>
                </div>
              </ItemSkill>
            </Item>
          </List>
        </div>
      </AboutContainer>
    </Content>
  );
}
